@charset "UTF-8";

html {
    font-size: 62.5%;
}

body {
    color: #333;
    font-size: 1.2rem;
    font-family: "Hiragino Kaku Gothic ProN", Meiryo, sans-serif;
}

a {
    color: #d03c56;
    text-decoration: none;
}

.header {
    width: 100%;
    padding: 28px 0px 10px;
    box-shadow: 0 0 10px 1px #e3e3e3;

    .logo {
        width: 225px;
        height: 56px;
        margin: 0 auto;
        background: url('../images/logo.png') no-repeat;

        a {
            display: block;
            width: 100%;
            height: 100%;
        }
    }

    .global-nav {
        margin-top: 15px;
        text-align: center;

        .nav-block {
            display: flex;
            justify-content:center;
            padding: 0;
        }

        .nav-item {
            display: inline-block;
            margin: 0 10px;

            a {
                display: inline-block;
                width: 100px;
                height: 30px;
                line-height: 30px;
                text-align: center;
                border-radius: 8px;
                color: #666;
                font-size: 1.3rem;
            }
            a:hover {
                background-color: #d03c56;
                color: #fff;
                transition: 0.15s;
            }
        }
    }
}

.wrapper {
    display: flex;
    width: 970px;
    margin: 30px auto 40px;
}

.main {
    display: block;
    float: left;
    width: 660px;
}

.side-menu {
    float: right;
    width: 275px;
}

.footer {
    width: 100%;
}